import "./Service.scss";
import Img1 from "../../images/5.png";
import Img2 from "./../../images/6.png";
import Img3 from "./../../images/7.png";
import Img4 from "./../../images/8.png";



const Service = ({ id }) => {
	const service = [
		{ id: "1", img: Img1, title: "Optimize speed for your page", desc: "Get know more about the topics" },
		{ id: "2", img: Img2, title: "Accessibility for mobile viewing", desc: "Lorem ipsum dolor sit amqu" },
		{ id: "3", img: Img3, title: "Learn More about Guidelines", desc: "Lorem ipsum dolor sit amet consecteturi qu" },
		{ id: "4", img: Img4, title: "Best Content idea for your page", desc: "Feel free to use this template for your business." },

	]
	return (
		<div className="service" id={id}>
			<div className="container">
				<h2>We Provide The Best Service With Our Tools</h2>
				<p className="serviceDesc">Lorem ipsum dolor sit, amet consectetur adipisicing elit. Ea magnam porro architecto saepe corrupti repellendus ex? ?</p>
				<div className="row">
					{service.map((data, index) => (
						<div class="col-lg-3 col-sm-6" key={index}>

							<div class="item">
								<div class="hidden-content">

									<p>{data.desc}</p>
								</div>
								<div class="showed-content">
									<img src={data.img} alt="" />
									<h4>{data.title}</h4>
								</div>
							</div>
						</div>))}
				</div>
			</div >
		</div >
	)
}

export default Service;