import { useState } from "react";
import Scrollspy from "react-scrollspy";
import MenuIcon from "./../../Svg/MenuIcon";
import "./Header.scss";

const Header = ({ tab }) => {
	const [isOpen, setOpen] = useState(false);
	return (
		<div className={`headerWrapper ${tab ? "stickyHeader" : false}`}>
			<div className="container">
				<div className="row">
					<div className="col-lg-2 col-4">
						<div className="logo">
							UI99
						</div>
					</div>
					<div className="col-lg-10 col-8">
						<div className="menu">
							<Scrollspy className="deskMenu" items={['hero', 'service', 'product', 'aboutus', 'partners', 'team', 'contact']}>
								<li><a href="#hero">Home</a></li>
								<li><a href="#service">Services</a></li>
								<li><a href="#product">Products</a></li>

								<li><a href="#aboutus">About Us</a></li>
								<li><a href="#partners">Partners</a></li>

								<li><a href="#team">Speakers</a></li>
								<li><a href="#contact">ContactUs</a></li>
							</Scrollspy >
							<div className="mobMenu">
								<button className="iconLinkBtn" onClick={() => setOpen(!isOpen)}><MenuIcon height="2rem" width="2rem" /></button>
							</div>
							{
								isOpen ?
									<ul className="mobMenuList">
										<li><a href="">Home</a></li>
										<li><a href="">Services</a></li>
										<li><a href="">Products</a></li>

										<li><a href="">About Us</a></li>
										<li><a href="">Partners</a></li>

										<li><a href="">Speakers</a></li>
										<li><a href="">ContactUs</a></li>
									</ul> : false
							}
						</div >
					</div >
				</div >
			</div >

		</div >
	)
}

export default Header;