

import "./Product.scss";
import Slider from "react-slick";
import Img from "../../images/portfolio-01.jpg";
import Img2 from "../../images/portfolio-02.jpg";
import Img3 from "../../images/portfolio-03.jpg";
import Img4 from "../../images/portfolio-04.jpg";



const Product = ({ id }) => {
	const settings = {
		infinite: true,
		slidesToShow: 4,
		slidesToScroll: 4,
		speed: 500,
		dots: true,
		responsive: [
			{
				breakpoint: 1025,
				settings: {
					slidesToShow: 3,
					slidesToScroll: 1,
				}
			},
			{
				breakpoint: 768,
				settings: {
					slidesToShow: 2,
					slidesToScroll: 2,
				}
			},
			{
				breakpoint: 600,
				settings: {
					slidesToShow: 1,
					slidesToScroll: 1,
				}
			},
		]

	}
	const products = [
		{ id: "1", img: Img, title: "Web Analysis", desc: "Lorem ipsum dolor si impedit officia nobis natus debitis, magni rerum a magnam repellendus, labore dolores mollitia." },
		{ id: "2", img: Img2, title: "Web Development", desc: "Lorem ipsum dolor si impedit officia nobis natus debitis, magni rerum a magnam repellendus, labore dolores mollitia." },
		{ id: "3", img: Img3, title: "UI/UX Design", desc: "Lorem ipsum dolor si impedit officia nobis natus debitis, magni rerum a magnam repellendus, labore dolores mollitia." },
		{ id: "4", img: Img4, title: "Mobile Apps", desc: "Lorem ipsum dolor si impedit officia nobis natus debitis, magni rerum a magnam repellendus, labore dolores mollitia." },
		{ id: "5", img: Img, title: "Web Analysis", desc: "Lorem ipsum dolor si impedit officia nobis natus debitis, magni rerum a magnam repellendus, labore dolores mollitia." },

	]
	return (
		<div className="product" id={id}>
			<div className="container-fluid">
				<div className="row">
					<div className="col-12">
						<h2>Our Products</h2>
						<p className="serviceDesc">Lorem ipsum dolor sit, amet consectetur adipisicing elit. Ea magnam porro architecto saepe corrupti repellendus ex? ?</p>

					</div>
					<div className="col-lg-12">
						<Slider {...settings}>
							{products.map((data, index) => (
								<div className="item" key={index}>
									<div className="thumb">
										<img src={data.img} alt="" />
										<div className="hover-effect">
											<div className="inner-content">
												<h2>{data.title}</h2>
												<p>{data.desc}</p>
											</div>
										</div>
									</div>
								</div>))}
						</Slider>
					</div>
				</div>
			</div>
		</div>
	)
}

export default Product;