
import SendIcon from "./../../Svg/SendIcon";
import "./ContactUs.scss";

const ContactUs = ({ id }) => {
	return (
		<section className="contact" id={id}>
			<div className="contactAbsolute" />
			<div class="container">
				<div class="row">
					<div class="col-lg-3 col-md-6 col-12">
						<div class="footer-item">
							<div class="logo">
								UI99
							</div>
							<p className="about-desc">UI99 is providing lost of things for your business like poster,presentation,web-design,templates.</p>

						</div>
					</div>
					<div class="col-lg-3 col-md-6 col-12">
						<div class="footer-item">
							<h4>Services</h4>
							<ul>
								<li><a href="#">SEO Development</a></li>
								<li><a href="#">Business Growth</a></li>
								<li><a href="#">Social Media Managment</a></li>
								<li><a href="#">Website Optimization</a></li>
							</ul>
						</div>
					</div>
					<div class="col-lg-3 col-md-6 col-12">
						<div class="footer-item">
							<h4>Community</h4>
							<ul>
								<li><a href="#">Digital Marketing</a></li>
								<li><a href="#">Business Ideas</a></li>
								<li><a href="#">Website Checkup</a></li>
								<li><a href="#">Page Speed Test</a></li>
							</ul>
						</div>
					</div>
					<div class="col-lg-3 col-md-6 col-12">
						<div class="footer-item">
							<h4>Subscribe Newsletters</h4>
							<p>Get our latest news and ideas to your inbox</p>
							<form action="#" method="get">
								<input type="text" name="email" id="email" pattern="[^ @]*@[^ @]*" placeholder="Your Email"
									required="" />
								<button type="submit" id="form-submit" class="main-button ">
									<SendIcon height="18px" width="18px" />
								</button>
							</form>
						</div>
					</div>
				</div>
			</div>
		</section>
	)
}

export default ContactUs;