import "./AboutUs.scss";
import Img from "./../../images/banner-right-image.png";
import Button from "../Button/Button";

const AboutUs = ({ id }) => {
	return (
		<div className="aboutUs" id={id}>
			<div className="container">
				<div className="row">
					<div className="col-lg-6 col-md-12 col-12">
						<figure>
							<img src={Img} alt="no-found" />
						</figure>
					</div>
					<div className="col-lg-6 col-md-12 col-12" data-aos="fade-down" data-aos-duration="4000" data-aos-delay="200">
						<div className="aboutDesc">
							<h2>About</h2>
							<p className="descAbout">UI99 is providing lost of things for your business like poster,presentation,web-design,templates. Lorem ipsum dolor sit amet consectetur adipisicing elit.</p>
							<p className="descAbout">Lorem ipsum dolor, sit amet consectetur adipisicing elit. Culpa officiis reprehenderit eveniet ipsum ipeos?</p>
							<Button className="primaryBtn" label="Read More" />
						</div>

					</div>
				</div>
			</div>
		</div>
	)
}

export default AboutUs;